.select {
  > .title {
    font-size: 20px;
    margin-top: 1em;
    padding: 1.2em 1.5em 1.5em;
  }
  .ask {
    font-size: 15px;
    font-weight: bold;
    margin: 0 2em 1em;
  }
  > .item {
    display: flex;
    align-items: center;
    padding: 0.8em;
    font-size: 15px;
    transition:all 0.5s;
    > .icon {
      font-size: 3em;
      pointer-events: none;
    }
    > .testWrapper {
      display: inline-block;
      margin: 0.5em;
      pointer-events: none;
      > .bold {
        font-weight: bold;
      }
    }
    &:hover {
      background-color: #e9e9e9;
    }
    &.active {
      background-color: #99c9ef;
      &:hover {
        background-color: #77b7ea;
        border: 1px #4c81ab;
      }
    }
  }
  > .buttonWrapper {
    padding-left: 4em;
    display: flex;
    align-items: center;
    > button {
      font-size: 14px;
      width: 200px;
      height: 32px;
      background: #ccc;
      border: none;
      position: absolute;
      right: 2em;
      bottom: 2em;
      &:hover {
        border: 2px solid #7a7a7a;
      }
    }
  }
}
