*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
*::after{
  box-sizing: border-box;
}
*::before{
  box-sizing: border-box;
}
ul,ol,dl{
  list-style:none;
}
a{
  color: inherit;
}
#root{
  background: url(../img/login.jpg) center center no-repeat;
  background-size: cover;
}